@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;800&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f0f2f5 !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.btn-success{
  font-size: 14px;
  border-color: #00BFA6;
  background-color: #00BFA6;
  transition: all 0.3s ease;
}

.btn-success:hover{
  border-color: #00BFA6;
  background-color: #00BFA6;
  opacity: 0.8;
}

.btn-primary{
  font-size: 14px;
  border-color: #00B0FF;
  background-color: #00B0FF;
  transition: all 0.3s ease;
}

.btn-primary:hover{
  border-color: #00B0FF;
  background-color: #00B0FF;
  opacity: 0.8;
}
.header-body .navbar{
    background-color: #00B0FF !important;
}

.header-body .navbar-brand img{
    width: 50px;
    padding: 0;
}

.header-body .navbar-brand:hover{
    color: #fff;
    font-weight: 600;
    opacity: 0.8;
    cursor: pointer;
}

.header-body .collapse{
    color: #fff !important;
}

.header-body .navbar-nav input[type="text"]{
    background-color: #b5e2f7;
    transition: 0.5s;
    width: 200px;
    padding: 0.8em 1.8em;
    border-radius: 25px;
}

.header-body .navbar-nav input[type="text"]:focus{
    background-color: #b5e2f7;
    width: 300px;
}

.header-body .navbar-nav ::-webkit-input-placeholder{
    color: #111 !important;
}

.header-body .navbar-nav :-ms-input-placeholder{
    color: #111 !important;
}

.header-body .navbar-nav ::placeholder{
    color: #111 !important;
}

.header-body .navbar-nav i.inverted.circular.icon{
    background-color: #00B0FF !important;
    margin-top: 1px;
    margin-right: 10px;
} 

.header-body li {
    list-style: none;
    margin-left: 5px;
    margin-right: 5px;
}

.header-body .list-right i.inverted.circular.icon{
    background-color: #b5e2f7 !important;
    color: #00B0FF !important;
    cursor: pointer;
    transition: 0.3s ease;
} 

.header-body .list-right i.inverted.circular.icon:hover{
    background-color: #f0f2f5 !important;
} 

.header-body .dropdown-toggle::after{
    content: none;
}

.header-body .dropdown-menu[data-bs-popper]{
    top: 125%;
}

.header-body .dropdown-menu{
    padding: 1em;
    background-color: #00B0FF;
    width: 350px;
}

.header-body .dropdown-menu .row{
    padding: 10px;
    cursor: default;
}

.header-body .dropdown-menu img{
    width: 60px;
    display: flex;
    margin:auto;
    border-radius: 100%;
}

.header-body .profile{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.header-body .profile div:nth-child(1){
    font-size: 24px;
    font-weight: 600;
    margin-bottom: 5px;
}

.header-body .menu ul{
    padding: 0;
}

.header-body .menu ul li{
    padding: 10px 0;
    cursor: pointer;
    transition: 0.3s ease;
    border-radius: 3px;
}

.header-body .menu ul li:hover{
    background-color: #b5e2f7;
}

.header-body .menu ul li span{
    margin-left: 10px;
}

.header-body .logout{
    padding: 10px 0;
    cursor: pointer;
    text-align: center;
    transition: 0.3s ease;
    border-radius: 3px;
    color: #111;
}

.header-body .logout:hover{
    background-color: #b5e2f7;
}

.header-body .logout span{
    margin-left: 10px;
}

@media only screen and (min-width:501px) and (max-width: 1000px) {
    .header-body .list-right {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .header-body li {
        margin-top: 5px;
        margin-bottom: 5px;
    }

    .header-body .dropdown-menu-end[data-bs-popper]{
        left: -350%; 
    }

    .header-body .profile{
        margin-top: 15px;
        text-align: center;
    }
}

@media(max-width: 500px) {
    .header-body .list-right {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .header-body li {
        margin-top: 5px;
        margin-bottom: 5px;
    }
    

    .header-body .dropdown-menu-end[data-bs-popper]{
        left: -350%; 
    }

    .header-body .profile{
        margin-top: 15px;
        text-align: center;
    }
}

.sider-body{
    padding-top: 70px;
    min-height: 91vh;
}

.sider-body .list-menu{
    font-size: 16px;
    font-family: 'Poppins', sans-serif;
}

.sider-body .name-menu{
    margin-left: 10px;
}

/* SIDE */
.sider-body .ui.thin.left.sidebar, .ui.thin.right.sidebar{
    width: 275px;
    background-color: #f0f2f5;
}

.sider-body .ui.labeled.icon.menu .item{
    color: #111;
    padding: 1.5em; 
    transition: all 0.3s ease;
}

.sider-body .ui.labeled.icon.menu .item:hover{
    background-color: #b5e2f7;
}

.sider-body .ui.left.visible.sidebar, .ui.right.visible.sidebar{
    box-shadow: none;
}

/* CONTENT */
.sider-body .ui.visible.thin.left.sidebar~.fixed, .ui.visible.thin.left.sidebar~.pusher{
    -webkit-transform: translate3d(275px,0,0) !important;
            transform: translate3d(275px,0,0) !important;
    background-color: #f0f2f5;
    max-width: 1165px;
    min-height: 91vh;
}

.sider-body .profile{
    padding: 2em 1em;
}

.sider-body .profile img{
    width: 60px;
}

.sider-body .name-profile{
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: start;
    margin-left: 10px;
}

.sider-body .name-profile div:nth-child(1){
    font-size: 18px;
    font-weight: bold;
}
.messanger-body{
    display: flex;
}

/* MESSANGER */
.messanger-body .messanger-section{
    flex: 8.5 1;
}

.messanger-body .messanger-top{
    height: 70vh;
    overflow-y: scroll;
    padding-right: 10px;
}

.messanger-body .messanger-bottom{
    margin-top: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.messanger-body .messanger-input{
    width: 90%;
    height: 90px;
    padding: 10px;
}

.messanger-body .messanger-send{
    width: 70px;
  height: 40px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}


/*  */
.messanger-body .message-body{
    display: flex;
    flex-direction: column;
    margin-top: 20px;
}

.messanger-body .message-body .message-top{
    display: flex;
}

.messanger-body .message-body .message-top img{
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}

.messanger-body .message-body .message-top p{
    padding: 10px;
    border-radius: 20px;
    background-color: #00B0FF;
    color: #fff;
    max-width: 500px;
}

.messanger-body .message-body .message-bottom{
    font-size: 12px;
    margin-bottom: 20px;
}

.message-body.own{
    align-items: flex-end;
}

.messanger-body .messanger-start{
    margin-top: 50px;
    text-align: center;
    font-size: 32px;
    font-weight: 500;
    color: #b5e2f7;
    cursor: default;

}


/* FRIEND */
.messanger-body .friend-section{
    margin-left: 10px;
    flex: 3.5 1;
}

.messanger-body .friend-container{
    display: flex;
    cursor: pointer;
    align-items: center;
    font-weight: 500;
    margin-top: 10px;
}

.messanger-body .friend-container:hover{
    background-color: #b5e2f7;
}

.messanger-body .friend-img-container{
    position: relative;
}

.messanger-body .friend-title{
    margin: 10px 0;
    font-size: 18px;
    font-weight: 500;
}

.messanger-body .friend-section img{
    width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  border: 1px solid white;
}

.messanger-body .friend-section span{
    margin-left: 10px;
}

.messanger-body .friend-online{
    width: 10px;
    height: 10px;
    background-color: limegreen;
    border-radius: 50%;
    position: absolute;
    top: 0px;
    right: 2px;
}
.login-body{
    max-width: 1300px;
    margin: auto;
    padding: 0 0.5em;
}

.login-body .row{
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
}

.login-body .left .title{
    padding: 2em;
    max-width: 1000;
    margin: auto;
}

.login-body .left .text div{    
    font-size: 32px;
    font-weight: 600;
    color: #00B0FF;
}

.login-body .left .image img{
    width: 300px;
    height: 300px;
}

.login-body .left .text p{
    font-size: 14px;
    font-weight: 500;
    text-align: justify;
}

.login-body .left .button{
    margin-top: 10px;
}

.login-body .right .card{
    padding: 2em;
    max-width: 28rem;
    margin: auto;
}

.login-body .right .title{
    text-align: center;
    color: #00BFA6;
    font-size: 24px;
    font-weight: 600;
    letter-spacing: 1px;
}

.login-body .right img{
    width: 100px;
    height: 100px;
}

.login-body .right .input{
   width: 100%;
}

.login-body .right .link{
    font-size: 12px;
}

.login-body .right .btn{
    width: 100%;
}

.login-body .ui.icon.input>i.icon{
    cursor: pointer !important;
}

.login-body .ui.icon.input>i.icon:not(.link){
    pointer-events: unset;
}
